/// This mixing allows for placing an icon either before or after using the
/// variable name from the Font Awesome 5 Pro package.
///
/// @author George Heimel
/// @author Steven Keiser
///
/// @param {variable} $fa-icon-variable
///   Which FontAwesome icon variable.
///   See "node_modules/@fortawesome/fontawesome-pro/scss/_variables.scss" for
///   all the available icon codes. The icon code can be constructed by
///   appending "$fa-var-" to the icon's name. e.g. "thumbs-up" becomes "$fa-var-thumbs-up".
///
/// @param {string} $fa-style ['light']
///   Which FontAwesome icon style.
///
/// @param {string} $position ['after']
///   Where to place the Font Awesome icon.
///
/// @example
///   @include fontawesome-icon('regular', $fa-var-thumbs-up, 'after');
///
/// @link https://fontawesome.com/icons/thumbs-up

@mixin font-awesome-icon(
  $fa-icon-variable,
  $fa-style: 'light',
  $position: 'after'
) {
  $font-awesome-styles: (
    'light': (
      'family': '"Font Awesome 5 Pro"',
      'weight': 300
    ),
    'regular': (
      'family': '"Font Awesome 5 Pro"',
      'weight': 400
    ),
    'solid': (
      'family': '"Font Awesome 5 Pro"',
      'weight': 900
    ),
    'brands': (
      'family': '"Font Awesome 5 Brands"',
      'weight': 400
    ),
    'duotone': (
      'family': '"Font Awesome 5 Duotone"',
      'weight': 900
    )
  );

  &::#{ $position } {
    $font-awesome-style: map-get($font-awesome-styles, $fa-style);

    content: fa-content(#{ $fa-icon-variable });
    display: inline-block;
    font-family: unquote(map-get($font-awesome-style, 'family'));
    font-style: normal;
    font-variant: normal;
    font-weight: map-get($font-awesome-style, 'weight');
    line-height: 1;
    text-rendering: auto;
    vertical-align: middle;

    // Might want to use this rather than hand type, just incase FA makes a change?
    // @include fa-icon;

    @if $position == 'before' {
      margin-right: 0.25em;
    }
    @else if $position == 'after' {
      margin-left: 0.25em;
    }
  }
}
